import React from 'react';
import './App.css';

const playersDefaultValue = `player1\nplayer2\nplayer3\nplayer4`
const rankingsDefaultValue = `player1:80\nplayer2:60\nplayer3:40\nplayer4:20`
let players = playersDefaultValue;
let rankings = rankingsDefaultValue;
let teamRed = [];
let teamWhite = [];

class RunButton extends React.Component {
  handleClick() {
    console.log('this is:', this);
  }

  render() {
    return (
      <button className="button" onClick={() => this.handleClick()}>
        Run
      </button>
    );
  }
}

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <p>Rosters</p>
      </header>
      <div className="App-content">
        <Team id="red" name="Red" players={teamRed} />
        <Team id="white" name="White" players={teamWhite} />
        <div className="row">
          <div id="players-column" className="column">
            <InputTextArea
              id="players"
              label="Players"
              value={players} />
          </div>
          <div id="players-column" className="column">
            <InputTextArea
              id="rankings"
              label="Rankings"
              value={rankings} />
          </div>
          <div id="run-button-column" className="column flex-end">
            <RunButton />
          </div>
        </div>
      </div>
    </div>
  );
}

const InputTextArea = (props) => {
  return (
    <div id={props.id}>
      <div>{props.label}</div>
      <textarea
        className="resize-none"
        rows="20"
        cols="12"
        defaultValue={props.value} />
    </div>
  )
}

const Team = (props) => {
  return (
    <div id="{props.id}">
      <div className="team-name">Team {props.name}</div>
      <div className="players-list">{props.players}</div>
    </div>
  )
}

export default App;
